import Link from "next/link"; // РюЁ рЃЊрЃљрЃљрЃЏрЃљрЃбрЃћ рЃћрЃА рЃ«рЃљрЃќрЃў
import Image from "next/image"; // РюЁ рЃЊрЃарЃЮрЃерЃўрЃА рЃўрЃЏрЃърЃЮрЃарЃбрЃў
import { useState } from "react"; // РюЁ рЃЊрЃљрЃЏрЃљрЃбрЃћрЃЉрЃБрЃџрЃўрЃљ useState рЃбрЃћрЃЦрЃАрЃбрЃўрЃА рЃЉрЃарЃБрЃюрЃЋрЃўрЃАрЃЌрЃЋрЃўрЃА
import { FaFacebook, FaInstagram, FaTiktok, FaTrophy, FaGamepad, FaGift } from "react-icons/fa";

export default function Navbar() {
  const [isRotating, setIsRotating] = useState(false);

  const handleClick = () => {
    setIsRotating(true); // рЃЉрЃарЃБрЃюрЃЋрЃўрЃА рЃЊрЃљрЃгрЃДрЃћрЃЉрЃљ
    setTimeout(() => setIsRotating(false), 2000); // 2 рЃгрЃљрЃЏрЃерЃў рЃњрЃљрЃЕрЃћрЃарЃЊрЃћрЃА
  };

  return (
    <nav className="bg-gradient-to-r from-blue-500 to-purple-600 p-5 shadow-lg fixed top-0 w-full z-50">
      <div className="container mx-auto flex justify-between items-center">

        {/* ­ЪЄг­ЪЄф рЃАрЃљрЃЦрЃљрЃарЃЌрЃЋрЃћрЃџрЃЮрЃА рЃЊрЃарЃЮрЃерЃћрЃЉрЃў (рЃЏрЃљрЃарЃфрЃ«рЃћрЃюрЃљ рЃЊрЃљ рЃЏрЃљрЃарЃ»рЃЋрЃћрЃюрЃљ) */}
        <div className="flex items-center space-x-2">
          <Image 
            src="/georgia-flag.png" 
            alt="Georgia Flag" 
            width={30} 
            height={25} 
            className="rounded-none" // РюЁ рЃЎрЃБрЃЌрЃ«рЃћрЃћрЃЉрЃў рЃљрЃдрЃљрЃа рЃЏрЃЮрЃЏрЃарЃњрЃЋрЃљрЃџрЃЊрЃћрЃЉрЃљ
          />

          {/* рЃЏрЃЮрЃФрЃарЃљрЃЋрЃў рЃбрЃћрЃЦрЃАрЃбрЃў */}
          <Link href="/" onClick={handleClick}>
            <span className={`text-white font-bold text-lg transition-transform duration-700 ${
              isRotating ? "animate-spin" : ""
            }`}>
              рЃЊрЃљрЃЏрЃљрЃЉрЃарЃБрЃюрЃћ
            </span>
          </Link>

          <Image 
            src="/georgia-flag.png" 
            alt="Georgia Flag" 
            width={30} 
            height={25} 
            className="rounded-none"
          />
        </div>

        {/* рЃЏрЃћрЃюрЃўрЃБрЃА рЃЉрЃЏрЃБрЃџрЃћрЃЉрЃў */}
        <ul className="flex space-x-4 text-lg font-semibold text-white justify-center w-full">
          <li className="nav-item"><Link href="/winners"><FaTrophy className="inline-block mr-2" /> рЃњрЃљрЃЏрЃљрЃарЃ»рЃЋрЃћрЃЉрЃБрЃџрЃћрЃЉрЃў</Link></li>
          <li className="nav-item"><Link href="/play"><FaGamepad className="inline-block mr-2" /> рЃЊрЃЮрЃЉрЃў</Link></li>
          <li className="nav-item"><Link href="/donate"><FaGift className="inline-block mr-2" /> рЃЊрЃЮрЃюрЃљрЃфрЃўрЃљ</Link></li>
          <li className="nav-item"><Link href="/contact">­ЪЊъ рЃЎрЃЮрЃюрЃбрЃљрЃЦрЃбрЃў</Link></li>
          <li className="nav-item"><Link href="/rules">­ЪЊю рЃгрЃћрЃАрЃћрЃЉрЃў</Link></li>
        </ul>

        {/* рЃАрЃЮрЃфрЃўрЃљрЃџрЃБрЃарЃў рЃЉрЃЏрЃБрЃџрЃћрЃЉрЃў */}
        <div className="flex space-x-6 text-white text-3xl">
          <a href="https://www.facebook.com/profile.php?id=61573014742100" target="_blank" rel="noopener noreferrer" className="social-icon">
            <FaFacebook />
          </a>
          <a href="https://www.instagram.com/luckyfromgeorgia/" target="_blank" rel="noopener noreferrer" className="social-icon">
            <FaInstagram />
          </a>
          <a href="https://www.tiktok.com/@lucky_from_georgia" target="_blank" rel="noopener noreferrer" className="social-icon">
            <FaTiktok />
          </a>
        </div>
      </div>
    </nav>
  );
}
