import { useEffect, useState } from "react";

export default function MoneyRain() {
  const [money, setMoney] = useState([]);
  const [totalAmount, setTotalAmount] = useState(0); // ­Ъњ░ рЃ»рЃљрЃЏрЃБрЃарЃў рЃЌрЃљрЃюрЃ«рЃљ
  const [playerPosition, setPlayerPosition] = useState(50); // ­ЪЈЃРђЇРЎѓ№ИЈ рЃЏрЃЮрЃЌрЃљрЃЏрЃљрЃерЃўрЃА X рЃърЃЮрЃќрЃўрЃфрЃўрЃљ
  const [isDancing, setIsDancing] = useState(false); // ­ЪЋ║ рЃЊрЃЮрЃЉрЃў рЃфрЃћрЃЎрЃЋрЃљрЃЋрЃА рЃЌрЃБ рЃљрЃарЃљ?
  const playerSpeed = 8; // ­ЪћЦ рЃАрЃгрЃарЃљрЃцрЃў рЃЏрЃЮрЃФрЃарЃљрЃЮрЃЉрЃљ!
  const moneyTypes = [
    { src: "/100lari.png", value: 100 },
    { src: "/50lari.png", value: 50 },
    { src: "/20lari.png", value: 100 },
    { src: "/200lari.png", value: 50 },
  ];

  useEffect(() => {
    const createMoney = () => {
      const newMoney = {
        id: Math.random(),
        left: Math.random() * 100 + "vw",
        animationDuration: Math.random() * 4 + 5 + "s",
        ...moneyTypes[Math.floor(Math.random() * moneyTypes.length)],
      };

      setMoney((prev) => [...prev, newMoney]);

      // РюЁ рЃЊрЃљрЃћрЃџрЃЮрЃЊрЃЮрЃА рЃЊрЃљрЃфрЃћрЃЏрЃљрЃА рЃЊрЃљ рЃерЃћрЃЏрЃЊрЃћрЃњ рЃерЃћрЃљрЃЏрЃЮрЃгрЃЏрЃЮрЃА рЃЏрЃЮрЃЌрЃљрЃЏрЃљрЃерЃўрЃА рЃЏрЃЊрЃћрЃЉрЃљрЃарЃћрЃЮрЃЉрЃљ
      setTimeout(() => {
        setMoney((prev) => prev.filter((item) => item.id !== newMoney.id));
        if (Math.abs(parseFloat(newMoney.left) - playerPosition) < 10) {
          collectMoney(newMoney); // РюЁ рЃЏрЃЮрЃЌрЃљрЃЏрЃљрЃерЃћрЃЏ рЃљрЃўрЃдрЃЮ рЃцрЃБрЃџрЃў!
        }
      }, (parseFloat(newMoney.animationDuration) * 2000));
    };

    const interval = setInterval(createMoney, 700);
    return () => clearInterval(interval);
  }, [playerPosition]);

  // ­Ъј« рЃЏрЃЮрЃЌрЃљрЃЏрЃљрЃерЃўрЃА рЃЏрЃЮрЃФрЃарЃљрЃЮрЃЉрЃљ (рЃЏрЃљрЃарЃфрЃ«рЃюрЃўрЃЋ рЃЊрЃљ рЃЏрЃљрЃарЃ»рЃЋрЃюрЃўрЃЋ)
  const handleKeyPress = (event) => {
    if (event.key === "ArrowLeft") {
      setPlayerPosition((prev) => Math.max(prev - playerSpeed, 0)); // рЃЏрЃљрЃарЃфрЃ«рЃюрЃўрЃЋ рЃАрЃгрЃарЃљрЃцрЃў
    } else if (event.key === "ArrowRight") {
      setPlayerPosition((prev) => Math.min(prev + playerSpeed, 100)); // рЃЏрЃљрЃарЃ»рЃЋрЃюрЃўрЃЋ рЃАрЃгрЃарЃљрЃцрЃў
    }
  };

  useEffect(() => {
    window.addEventListener("keydown", handleKeyPress);
    return () => window.removeEventListener("keydown", handleKeyPress);
  }, []);

  // РюЁ рЃЏрЃЮрЃЌрЃљрЃЏрЃљрЃерЃўрЃА рЃЏрЃўрЃћрЃа рЃцрЃБрЃџрЃўрЃА рЃљрЃдрЃћрЃЉрЃљ (рЃЕрЃљрЃЋрЃљрЃЏрЃљрЃбрЃЮрЃЌ рЃАрЃљрЃцрЃБрЃџрЃћрЃерЃў)
  const collectMoney = (moneyItem) => {
    setTotalAmount((prev) => {
      const newTotal = prev + moneyItem.value;

      // ­Ъћё рЃарЃЮрЃфрЃљ 1000РѓЙ-рЃЏрЃЊрЃћ рЃљрЃЋрЃљ, рЃЊрЃЮрЃЉрЃў рЃЮрЃарЃ»рЃћрЃа рЃўрЃфрЃћрЃЎрЃЋрЃћрЃЉрЃА!
      if (newTotal >= 2000) {
        setIsDancing(true);

        // ­ЪЋ║ 1.5 рЃгрЃљрЃЏрЃў рЃфрЃћрЃЎрЃЋрЃљ
        setTimeout(() => {
          setIsDancing(false); // рЃерЃћрЃЕрЃћрЃарЃћрЃЉрЃљ
          
          // ­ЪЋ║ 1 рЃгрЃљрЃЏрЃерЃў рЃЎрЃЋрЃџрЃљрЃЋ рЃфрЃћрЃЎрЃЋрЃљ
          setTimeout(() => {
            setIsDancing(true);

            // ­ЪЋ║ 1.5 рЃгрЃљрЃЏрЃерЃў рЃўрЃАрЃћрЃЋ рЃерЃћрЃЕрЃћрЃарЃЊрЃћрЃЉрЃљ
            setTimeout(() => {
              setIsDancing(false);
              setTotalAmount(0); // ­Ъћё рЃАрЃљрЃцрЃБрЃџрЃћ рЃюрЃБрЃџрЃЊрЃћрЃЉрЃљ
            }, 1500);
            
          }, 2000);
          
        }, 1500);
      }

      return newTotal;
    });
    playSound(); // ­Ъјх рЃцрЃБрЃџрЃўрЃА рЃљрЃдрЃћрЃЉрЃўрЃА рЃ«рЃЏрЃљ
  };

  // ­Ъјх рЃ«рЃЏрЃљ рЃцрЃБрЃџрЃўрЃА рЃљрЃдрЃћрЃЉрЃўрЃАрЃљрЃА
  const playSound = () => {
    const audio = new Audio("/coin.mp3"); // рЃЊрЃљрЃљрЃЏрЃљрЃбрЃћ `public/coin.mp3`
    audio.volume = 0.5;
    audio.play();
  };

  return (
    <div className="fixed top-0 left-0 w-full h-full pointer-events-none z-50">
      {/* ­ЪїД№ИЈ рЃцрЃБрЃџрЃўрЃА рЃгрЃЋрЃўрЃЏрЃљ */}
      {money.map((item) => (
        <img
          key={item.id}
          src={item.src}
          alt="Money"
          className="absolute w-16 h-10"
          style={{
            left: item.left,
            animation: `fall ${item.animationDuration} linear forwards`,
          }}
        />
      ))}

      {/* ­ЪЈЃРђЇРЎѓ№ИЈ рЃЏрЃЮрЃЌрЃљрЃЏрЃљрЃерЃћ рЃљрЃю рЃфрЃћрЃЎрЃЋрЃљрЃЋрЃА */}
      <img
        src="/player.png"
        alt="Player"
        className={`absolute w-30 h-56 transition-all duration-500 ${
          isDancing ? "dance" : ""
        }`}
        style={{
          left: isDancing ? "50%" : `${playerPosition}vw`,
          bottom: isDancing ? "30%" : "-20px",
          transform: isDancing ? "translateX(-50%) scale(1.2)" : "none",
        }}
      />

      {/* ­ЪЉю рЃАрЃљрЃцрЃБрЃџрЃћ + рЃЌрЃљрЃюрЃ«рЃљ */}
      <div className="absolute bottom-10 left-10 flex items-center bg-green-800 text-black p-4 rounded-lg shadow-lg">
        <img src="/wallet.png" alt="Wallet" className="w-16 h-14 mr-2" />
        <p className="text-xl font-bold">{totalAmount}РѓЙ</p>
      </div>

      <style jsx>{`
        @keyframes fall {
          from {
            transform: translateY(-10%);
            opacity: 1;
          }
          to {
            transform: translateY(100vh);
            opacity: 0;
          }
        }

        /* ­ЪЋ║ рЃЊрЃЮрЃЉрЃўрЃА рЃфрЃћрЃЎрЃЋрЃўрЃА рЃљрЃюрЃўрЃЏрЃљрЃфрЃўрЃљ */
        @keyframes danceAnimation {
          0% { transform: translateX(-50%) scale(1.2) rotate(0deg); }
          25% { transform: translateX(-50%) scale(1.2) rotate(10deg); }
          50% { transform: translateX(-50%) scale(1.2) rotate(-10deg); }
          75% { transform: translateX(-50%) scale(1.2) rotate(10deg); }
          100% { transform: translateX(-50%) scale(1.2) rotate(0deg); }
        }

        .dance {
          animation: danceAnimation 0.5s infinite alternate ease-in-out;
        }
      `}</style>
    </div>
  );
}
